'use client';

import React, { useEffect, useState, useRef } from 'react';
import { signOut, onAuthStateChanged } from 'firebase/auth';
import { auth } from '../../config/firebaseConfig';
import { useRouter } from 'next/navigation';
import dynamic from 'next/dynamic';

// Load Spline animation without SSR
const Spline = dynamic(() => import('@splinetool/react-spline'), {
  ssr: false,
});

export default function DashboardPage() {
  const [user, setUser] = useState(null);
  const router = useRouter();
  const emailRef = useRef(null);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
      } else {
        router.push('/');
      }
    });
    return () => unsubscribe();
  }, []);

  const handleLogout = async () => {
    await signOut(auth);
    router.push('/');
  };

  const scrollToEmail = () => {
    if (emailRef.current) {
      emailRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <>
      {/* Bounce keyframes animation definition */}
      <style jsx>{`
        @keyframes bounce {
          0%, 20%, 50%, 80%, 100% {
            transform: translateY(0);
          }
          40% {
            transform: translateY(8px);
          }
          60% {
            transform: translateY(4px);
          }
        }
      `}</style>

      <div style={styles.pageWrapper}>
        {/* Navbar */}
        <div style={styles.navbar}>
          <ul style={styles.navLinks}>
            <li style={{ ...styles.navItem, ...styles.activeLink }}>All</li>
            <li style={styles.navItem}>Products</li>
            <li style={styles.navItem}>Pricing</li>            
            <li style={styles.navItem}>Tools</li>
            <li style={styles.navItem}>Contact</li>
          </ul>
          {user && (
            <button onClick={handleLogout} style={styles.logoutButton}>
              Logout
            </button>
          )}
        </div>

        {/* Background Animation */}
        <div style={styles.splineWrapper}>
          <Spline scene="https://prod.spline.design/QtHlQRQS4fdCbgAt/scene.splinecode" />
        </div>

        {/* Hero Section */}
        <div style={styles.heroContent}>
          <h1 style={styles.heroHeading}>Welcome to the Dashboard! ðŸŽ‰</h1>
          <button onClick={scrollToEmail} style={styles.getStartedButton}>
            Get Started
          </button>
        </div>

        {/* Scroll Down Indicator */}
        <div style={styles.scrollIndicator} onClick={scrollToEmail}>
          <div style={styles.semiCircle} />
          <span style={styles.scrollText}>Scroll</span>
        </div>

        {/* Scroll Target (for smooth scroll) */}
        <div ref={emailRef} style={styles.scrollTarget}></div>
      </div>
    </>
  );
}

// Style definitions
const styles = {
  pageWrapper: {
    position: 'relative',
    height: '100vh',
    width: '100%',
    fontFamily: 'Segoe UI, sans-serif',
    overflowX: 'hidden',
    overflowY: 'auto',
    backgroundColor: '#1c109d',
  },
  navbar: {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100%',
    padding: '0.6rem 2rem',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    background: '#1c109d',
    zIndex: 10,
  },
  navLinks: {
    listStyle: 'none',
    display: 'flex',
    gap: '2.5rem',
    margin: 0,
    padding: 0,
  },
  navItem: {
    color: '#dcd7ff',
    fontSize: '1rem',
    fontWeight: 500,
    cursor: 'pointer',
    transition: 'background 0.2s ease, color 0.2s ease',
    padding: '0.5rem 1rem',
    borderRadius: '6px',
  },
  activeLink: {
    color: '#fcd34d',
    backgroundColor: '#2a1aa5',
  },
  logoutButton: {
    padding: '0.6rem 1.5rem',
    backgroundColor: '#a78bfa',
    color: '#1c109d',
    fontWeight: 'bold',
    fontSize: '1rem',
    border: 'none',
    borderRadius: '999px',
    cursor: 'pointer',
  },
  splineWrapper: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    zIndex: 0,
  },
  heroContent: {
    position: 'relative',
    zIndex: 2,
    top: '38vh',
    textAlign: 'center',
    color: '#fff',
  },
  heroHeading: {
    fontSize: '2.8rem',
    color: '#fff',
    marginBottom: '1rem',
  },
  getStartedButton: {
    backgroundColor: '#a78bfa',
    color: '#1c109d',
    padding: '0.75rem 2rem',
    fontSize: '1rem',
    fontWeight: 'bold',
    borderRadius: '999px',
    border: 'none',
    cursor: 'pointer',
    transition: 'opacity 0.3s ease',
  },
  scrollIndicator: {
    position: 'absolute',
    top: '78vh',
    left: '50%',
    transform: 'translateX(-50%)',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    zIndex: 3,
    cursor: 'pointer',
  },
  semiCircle: {
    width: '40px',
    height: '20px',
    borderBottom: '3px solid #a78bfa',
    borderRadius: '0 0 30px 30px',
    animation: 'bounce 2s infinite',
  },
  scrollText: {
    marginTop: '8px',
    color: '#a78bfa',
    fontSize: '0.85rem',
  },
  scrollTarget: {
    height: '300px',
  },
};
